.Container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.initDiv{
    background: #ffffff;
    border: 7px solid #c4a91b;

    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    animation: exiting 1s forwards ease-in-out;
}

@keyframes exiting {
    0% {opacity: 1}
    99% {opacity: 0; width: 100%; height: 100%}
    100% {opacity: 0; display: none; width: 0; height: 0;}
}

.linkTextContext{
    height: 80%;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.linkTitle{
    color: #000000;
    font-family: 'Tenor Sans', 'Aileron thin', sans-serif;
    font-size: 23px;
    font-weight: bold;
    text-align: center;
}


@media screen and (max-width: 768px) {
    .Container{
        flex-direction: column;
    }
}