.divBox{
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.cardLinkBox{
    text-decoration: none;
    height: 200px;
    width: 200px;
    border-radius: 50%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    user-select: none;

    position: relative;
    animation: floatingX 2s infinite linear, floatingY 3s infinite linear;

    outline-offset: -100px;
    cursor: pointer;
    transition: 0.3s;
}

.blueColor{
    background: #ffffff;
    border: 7px solid #07827f;
}
.blueColor:hover{
    background: #07827f;
    transition: all 250ms ease-in-out;

    outline: 8px solid #07827f;
    outline-offset: 14px;
}
.yellowColor{
    background: #ffffff;
    border: 7px solid #c4a91b;
}
.yellowColor:hover{
    background: #c4a91b;
    transition: all 250ms ease-in-out;

    outline: 8px solid #c4a91b;
    outline-offset: 14px;
}


.cardLinkBox:nth-child(1){
    animation-delay: 0.7s, 2.7s, 2.7s;
}
.cardLinkBox:nth-child(2){
    animation-delay: 0.5s, 2.5s, 2.5s;
}
.cardLinkBox:nth-child(3){
    animation-delay: 0.3s, 2.3s, 2.3s;
}
.cardLinkBox:nth-child(4){
    animation-delay: 0.1s, 2.1s, 2.1s;
}

@keyframes floatingX {
    0% {left: 0px;}
    33% {left: 2.5px;}
    66% {left: -2.5px;}
    100% {left: 0px;}
}

@keyframes floatingY {
    0% {top: 0px;}
    33% {top: 2.5px;}
    66% {top: -2.5px;}
    100% {top: 0px;}
}

.linkTextContext{
    height: 80%;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.linkTitle{
    color: #000000;
    font-family: 'Tenor Sans', 'Aileron thin', sans-serif;
    font-size: 23px;
    font-weight: bold;
    text-align: center;
}
